.gallery:hover {
  cursor: pointer;
}
.gallery figure {
  box-sizing: border-box;
}
.gallery figure img {
  width: 100%;
  height: 100%;
}
.gallery figure figcaption {
  max-width: 100%;
  box-sizing: border-box;
}
.gallery .overlay {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.85);
  max-width: 0;
  max-height: 0;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  border: 0;
  padding: 0;
  box-sizing: border-box;
  z-index: -1;
}
.gallery button {
  position: absolute;
  margin: 2rem;
  outline: none;
}
.gallery .close {
  top: 0;
}
.gallery .prev {
  left: 1rem;
}
.gallery .next {
  right: 1rem;
}
.gallery.open .overlay[open],
.gallery.open .overlay.open {
  max-width: 100%;
  max-height: 100%;
  opacity: 1;
  z-index: 999;
}
.gallery.open .overlay[open] .overlay-content,
.gallery.open .overlay.open .overlay-content {
  background: #fff;
  max-width: 95%;
  max-height: 95%;
  min-width: 100px;
  min-height: 100px;
  margin: 2.5%;
  box-sizing: border-box;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
}
.gallery.open .overlay[open] .overlay-content .current,
.gallery.open .overlay.open .overlay-content .current {
  position: relative;
  margin: 0;
}
.gallery.open .overlay[open] .overlay-content .current img,
.gallery.open .overlay.open .overlay-content .current img,
.gallery.open .overlay[open] .overlay-content .current video,
.gallery.open .overlay.open .overlay-content .current video,
.gallery.open .overlay[open] .overlay-content .current object,
.gallery.open .overlay.open .overlay-content .current object,
.gallery.open .overlay[open] .overlay-content .current embed,
.gallery.open .overlay.open .overlay-content .current embed,
.gallery.open .overlay[open] .overlay-content .current iframe,
.gallery.open .overlay.open .overlay-content .current iframe {
  max-width: 100%;
  max-height: 100%;
  margin-bottom: -2px;
}
.gallery.open .overlay[open] .overlay-content .current figcaption,
.gallery.open .overlay.open .overlay-content .current figcaption {
  position: absolute;
  bottom: 0;
  left: 0;
}
